@import url(https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,300;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
    height: 100%; 
    margin: 0;
    overflow-x: hidden;
}
body {
  /* padding-top: 4.2rem; */
  font-family: "Kanit", sans-serif;
  /* padding-bottom: 4.2rem; */
  background: #031a5e;
}

a {
  text-decoration: none !important;
}

.bg_register_user {
  padding: 20px;
  background: linear-gradient(180deg, #ffe867 10%, #fe8a02);
  border-radius: 15px;
  color: #000;
  font-weight: bold;
  margin: 30px 0;
}
.bg_myform_1 {
  background: linear-gradient(#061ab1, #041c7c);
}
.bg_myform_2 {
  background: linear-gradient(#3a3a3a, #121212);
}

.myform {
  position: relative;
  font-family: "Kanit", sans-serif;
  display: flex;
  padding: 25px 35px;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 1.1rem;
  outline: 0;
  max-width: 500px;
}

.myform h2 {
  color: #fec33f;
}
.myform input,
.myform input:focus,
.myform select {
  border: 1px solid #fec33f;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
}

.input-group-text {
  background: linear-gradient(160deg, #b47e11, #fec33f, #e09c14);
  border: 1px solid #fec33f !important;
  color: #343a40;
}

.myform input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #bbb;
}

.myform input::-moz-placeholder {
  /* Firefox 19+ */
  color: #bbb;
}

.myform input:-ms-input-placeholder {
  /* IE 10+ */
  color: #bbb;
}

.myform input:-moz-placeholder {
  /* Firefox 18- */
  color: #bbb;
}

.tx-tfm {
  text-transform: uppercase;
}

.btn_login {
  background: linear-gradient(160deg, #b47e11, #fec33f, #e09c14);
}

.mybtn {
  border-radius: 50px;
}

.login-or {
  position: relative;
  color: #aaa;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.span-or {
  display: block;
  position: absolute;
  left: 34%;
  top: -2px;
  background: var(--bg_login_2);
  width: 125px;
  text-align: center;
  color: #fff;
}

.hr-or {
  height: 1px;
  margin-top: 0px !important;
  margin-bottom: 0px !important;
  border-color: #ccc;
}

.google {
  color: #fec33f;
  width: 100%;
  height: 40px;
  text-align: center;
  outline: none;
  border: 1px solid lightgrey;
  transition: all 0.5s;
  font-family: "Kanit", sans-serif;
}

.google:hover {
  color: #000;
  background: linear-gradient(160deg, #b47e11, #fec33f, #e09c14);
}

form .error {
  color: #ff0000;
}

#second {
  display: none;
}

.icon {
  position: relative;
  overflow: hidden;
  text-decoration: none;
}

.text-right {
  text-align: right;
}

#msform fieldset:not(:first-of-type) {
  display: none;
}
#msform .action-button-previous {
  color: #eee;
}

select.list-dt {
  border: none;
  outline: 0;
  border-bottom: 1px solid #ccc;
  padding: 2px 5px 3px 5px;
  margin: 2px;
}

select.list-dt:focus {
  border-bottom: 2px solid skyblue;
}

.card {
  z-index: 0;
  border: none;
  border-radius: 0.5rem;
  position: relative;
}

#progressbar {
  margin-bottom: 30px;
  overflow: hidden;
  color: lightgrey;
}

#progressbar .active {
  color: #000000;
}

#progressbar li {
  list-style-type: none;
  font-size: 12px;
  width: 25%;
  float: left;
  position: relative;
}

#progressbar #account:before {
  font-family: FontAwesome;
  content: "\F023";
}

#progressbar #personal:before {
  font-family: FontAwesome;
  content: "\F007";
}

#progressbar #payment:before {
  font-family: FontAwesome;
  content: "\F09D";
}

#progressbar #confirm:before {
  font-family: FontAwesome;
  content: "\F00C";
}

#progressbar li:before {
  width: 50px;
  height: 50px;
  line-height: 45px;
  display: block;
  font-size: 18px;
  color: #ffffff;
  background: lightgray;
  border-radius: 50%;
  margin: 0 auto 10px auto;
  padding: 2px;
}

#progressbar li:after {
  content: "";
  width: 100%;
  height: 2px;
  background: lightgray;
  position: absolute;
  left: 0;
  top: 25px;
  z-index: -1;
}

#progressbar li.active:before,
#progressbar li.active:after {
  background: skyblue;
}

.radio-group {
  position: relative;
  margin-bottom: 25px;
}

.radio {
  display: inline-block;
  width: 204;
  height: 104;
  border-radius: 0;
  background: lightblue;
  box-shadow: 0 2px 2px 2px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  cursor: pointer;
  margin: 8px 2px;
}

.radio:hover {
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.3);
}

.radio.selected {
  box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.1);
}

.fit-image {
  width: 100%;
  object-fit: cover;
}
#forgotModal {
  font-family: "Kanit", sans-serif;
}
.modal-content {
  background: #191818;
  color: #fff;
}
.btn-blu-grow {
  border: 2px solid #fff !important;
  background: linear-gradient(0deg, #a25700, #fc0) !important;
  color: #fff;
  margin: auto;
  position: relative;
  border-radius: 20px;
  display: block;
}
.btn-blu-grow:before {
  content: "";
  position: absolute;
  left: -5px;
  top: -5px;
  width: calc(100% + 7px);
  height: calc(100% + 7px);
  background: 0 0;
  z-index: 1;
  border: 5px solid #fadb0a;
  border-radius: 24px;
  -webkit-filter: blur(5px);
  filter: blur(5px);
}

.swal-text,
.swal-title {
  font-family: "Kanit", sans-serif;
}
.swal-text {
  font-size: 18px;
}
.swal-footer {
  text-align: center;
}
.l-radio {
  padding: 6px;
  border-radius: 50px;
  display: inline-flex;
  cursor: pointer;
  transition: background 0.2s ease;
  margin: 12px 0;
  -webkit-tap-highlight-color: transparent;
}
.l-radio:hover,
.l-radio:focus-within {
  background: rgba(159, 159, 159, 0.1);
}
.l-radio input {
  vertical-align: middle;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  background: none;
  border: 0;
  box-shadow: inset 0 0 0 1px #9f9f9f;
  box-shadow: inset 0 0 0 1.5px #9f9f9f;
  -webkit-appearance: none;
          appearance: none;
  padding: 0;
  margin: 0;
  transition: box-shadow 150ms cubic-bezier(0.95, 0.15, 0.5, 1.25);
  pointer-events: none;
}
.l-radio input:focus {
  outline: none;
}
.l-radio input:checked {
  box-shadow: inset 0 0 0 6px #fec33f;
}
.l-radio span {
  color: #fff;
  vertical-align: middle;
  display: inline-block;
  line-height: 20px;
  padding: 0 8px;
}
.preloader {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  left: 0;
  z-index: 9;
  background: rgba(0, 0, 0, 0.7);
}
.circular-spinner {
  margin-top: 30px;
  width: 60px;
  height: 60px;
  border: 7px solid white;
  border-top: 7px solid #fc0;
  border-radius: 50%;
  -webkit-animation: rotate 0.5s linear infinite;
          animation: rotate 0.5s linear infinite;
  position: absolute;
  left: 49%;
  top: 45%;
}
.btn-block {
  display: block;
  width: 100%;
}
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@media (max-width: 990px) {
  .circular-spinner {
    top: 30%;
    left: 45%;
  }
  .txt_logout {
    right: 0px;
  }
}
@media (max-width: 600px) {
  .span-or {
    left: 27%;
  }
}
@media (min-width: 768px) {
  .col-md-12 {
    padding-right: 15px;
    padding-left: 15px;
  }
  .col-md-5 {
    width: 38%;
  }
}

.txt_head_set h4 {
  color: #f5c94c;
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 0;
}
.txt_logout {
  min-width: 100px;
  max-width: 100px;
  margin: auto;
  text-align: center;
  position: relative;
  right: -15px;
}

/* home */
