.img_icon_set {
  max-width: 25px;
  margin: auto;
  display: block;
  position: relative;
  top: 15px;
}
.section_menu h5 {
  font-weight: 400;
  margin-top: 7px;
  font-size: 16px;
}
.animation-gold {
  -webkit-animation-name: x-neon-gold !important;
  /* animation-name: x-neon-gold !important; */
  -webkit-animation: x-neon-gold 0.8s ease-in-out infinite alternate;
  /* animation: x-neon-gold 0.8s ease-in-out infinite alternate; */
}
@-webkit-keyframes x-neon-gold {
  0% {
    box-shadow: 0 0 5px #fff, 0 0 8px #fff, 0 0 10px #fff, 0 0 13px #ffdc73,
      0 0 16px #ffdc73, 0 0 28px #ffdc73, 0 0 36px #ffdc73, 0 0 44px #ffdc73;
  }
  to {
    box-shadow: 0 0 2px #fff, 0 0 5px #fff, 0 0 8px #fff, 0 0 10px #ffdc73,
      0 0 8px #ffdc73, 0 0 14px #ffdc73, 0 0 18px #ffdc73, 0 0 22px #ffdc73;
  }
}
@keyframes x-neon-gold {
  0% {
    box-shadow: 0 0 5px #fff, 0 0 8px #fff, 0 0 10px #fff, 0 0 13px #ffdc73,
      0 0 16px #ffdc73, 0 0 28px #ffdc73, 0 0 36px #ffdc73, 0 0 44px #ffdc73;
  }
  to {
    box-shadow: 0 0 2px #fff, 0 0 5px #fff, 0 0 8px #fff, 0 0 10px #ffdc73,
      0 0 8px #ffdc73, 0 0 14px #ffdc73, 0 0 18px #ffdc73, 0 0 22px #ffdc73;
  }
}
.btn-gold {
  position: relative;
  top: -20px;
  border-radius: 50%;
  min-width: 85px;
  min-height: 85px;
  max-width: 85px;
  max-height: 85px;
  margin: auto;
  color: #fff !important;
  background: linear-gradient(0deg, #8a4a00, #fc0) !important;
  transition: all 0.5s ease;
}
.menu_txt {
  color: #fff;
}

/* profile */
